import React from "react";
import { push } from "connected-react-router";
import {
  Card,
  CardMedia,
  Typography,
  CardContent,
  CardActionArea,
  Chip,
} from "@material-ui/core";
import { makeStyles, Theme } from "@material-ui/core/styles";
import { thumbnailsFinder } from "../../../_helpers/constants";
import { store } from "../../../_store/store.config";
import { ICardDetail } from "../../../_types";

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    width: 300,
    height: 260,
    margin: theme.spacing(1),
  },
  cardAction: {
    justifyContent: "space-between",
    margin: "inherit",
  },
  content: {
    minHeight: 130,
  },
  rightChip: {
    position: "absolute",
    cursor: "pointer",
    margin: theme.spacing(1),
    backgroundColor: "green",
  },
  leftChip: {
    position: "absolute",
    right: 0,
    cursor: "pointer",
    margin: theme.spacing(1),
  },
}));

type Props = {
  courseItem: ICardDetail;
  index: number;
  type: string;
  path: string;
};

export const CourseItem: React.FC<Props> = ({
  index,
  type,
  courseItem,
  path,
}) => {
  const classes = useStyles();
  const { title, id, thumbnails, info } = courseItem;

  const clickHandler = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ): void => {
    store.dispatch(push(path + "/" + type + id));
  };
  return (
    <Card className={classes.root}>
      <CardActionArea onClick={clickHandler}>
        <Chip label={index + 1} className={classes.rightChip} />
        <Chip
          label={info.grade?.name}
          className={classes.leftChip}
          color="secondary"
        />
        <CardMedia
          component="img"
          alt={title}
          height="130"
          image={thumbnailsFinder(thumbnails)}
          title={title}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="body1" component="h6">
            {title}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
};

