import { ThunkDispatch } from "redux-thunk";
import {
  AppState,
  ICardDetail,
  SAVE,
  FAILURE,
  REQUEST,
  SUCCESS,
  GuideSelect,
} from "../_types";
import { API } from "../_helpers/api";
import { AppActions } from "../_types";
import { requestCourse } from "./course.actions";
import { PathFinder } from "../_helpers/constants";
import { requestExercise } from "./exercise.actions";
import { errorHandler } from "../components/Layout/SnackBar/alert";

export const requestGuide = (path: PathFinder) => (
  dispatch: ThunkDispatch<AppState, undefined, AppActions>,
  getState: () => AppState
) => {
  const { guide, ...newPath } = path;
  const found = getState().bank.guides.data.find(
    (guide) => guide.id === path.guide
  );
  if (found?.guide_select) {
    dispatch({ type: SUCCESS.GUIDE, data: found });
    dispatch({ type: SAVE.COURSES, data: addCourses(found.guide_select) });
    dispatch({ type: SAVE.EXERCISES, data: addExercises(found.guide_select) });
    if (path.course) dispatch(requestCourse(newPath));
    if (path.exercise) dispatch(requestExercise(newPath));
  } else {
    dispatch({ type: REQUEST.GUIDE });
    API.get(`/lesson/v2/guide?id=${path.guide}`)
      .then((res) => {
        dispatch({ type: SUCCESS.GUIDE, data: res.data[0] });
        dispatch({ type: SAVE.GUIDES, data: res.data });
        dispatch({
          type: SAVE.COURSES,
          data: addCourses(res.data[0].guide_select),
        });
        dispatch({
          type: SAVE.EXERCISES,
          data: addExercises(res.data[0].guide_select),
        });
        if (path.course) dispatch(requestCourse(newPath));
        if (path.exercise) dispatch(requestExercise(newPath));
      })
      .catch((err) => errorHandler(err, FAILURE.GUIDE));
  }
};

const addCourses = (guide_select: GuideSelect[]) => {
  const added: ICardDetail[] = [];
  guide_select.map((course) =>
    course.course?.map((lesson) => added.push(lesson))
  );
  return added;
};
const addExercises = (guide_select: GuideSelect[]) => {
  const added: ICardDetail[] = [];
  guide_select.map((course) =>
    course.exercise?.map((exercise) => added.push(exercise))
  );
  return added;
};
